import { MouseEventHandler } from 'react'; // Importamos el tipo MouseEventHandler de React
import Button from '@mui/material/Button'; // Importamos el botn de Material UI

// Declaro la interface del componente MyButton.
export interface MyButtonProps {
  text: string; // Prop 'text' de tipo string: texto que se mostrar en el botn
  txtcolor: string; // Prop 'txtcolor' de tipo string: color del texto del botn
  bgcolor: string; // Prop 'bgcolor' de tipo string: color de fondo del botn
  borderColor: string; // Prop 'borderColor' de tipo string: color del borde del botn
  borderWidth: string; // Prop 'borderWidth' de tipo string: ancho del borde del botn
  size?: 'small' | 'medium' | 'large'; // Prop 'size': tamao del botn (opcional)
  hoverTxtColor?: string; // Prop 'hoverTxtColor': color del texto al hacer hover (opcional)
  hoverBgColor?: string; // Prop 'hoverBgColor': color de fondo al hacer hover (opcional)
  disabled?: boolean; // Prop 'disabled': indica si el botn est deshabilitado (opcional)
  onClick?: MouseEventHandler<HTMLButtonElement>; // Prop 'onClick': funcin que se ejecuta al hacer clic en el botn (opcional)
}

// Defino mi componente MyButton y le digo que las props son las que defin arriba
function MyButton(props: MyButtonProps) {
  // Paso el valor de las props a un objeto:
  const { bgcolor, text, txtcolor, borderColor, borderWidth, size, hoverTxtColor, hoverBgColor, disabled, onClick } = props;

  return (
    // Uso el Button de la librera MUI y lo personalizo con las props que pasan a mi componente.
    <Button
      variant="outlined" // Usamos el estilo "outlined" para incluir el borde
      type='button'
      onClick={onClick}
      disabled={disabled}
      size={size}
      sx={{
        backgroundColor: bgcolor, // Color de fondo del botn
        color: txtcolor, // Color del texto del botn
        borderColor: borderColor, // Color del borde del botn
        borderWidth: borderWidth, // Ancho del borde del botn
        ':hover': { // Estilos para el estado hover
          backgroundColor: hoverBgColor, // Color de fondo al pasar el ratn
          color: hoverTxtColor // Color del texto al pasar el ratn
        }
      }}
    >
      {text} {/* Muestra el texto del botn */}
    </Button>
  );
}

export default MyButton; // Exportamos el componente MyButton
