import React, { useState } from 'react'; // Importa React y el hook useState para manejar el estado
import { TextField, Button, Typography, Card } from '@mui/material'; // Importa componentes de Material UI

// Define las propiedades que acepta el componente ContactForm
interface ContactFormProps {
  onClose: () => void; // Funcin para cerrar el formulario
  onThankYou: () => void; // Nueva prop para manejar el agradecimiento
}

// Componente funcional ContactForm que recibe props
const ContactForm: React.FC<ContactFormProps> = ({ onClose, onThankYou }) => {
  const [name, setName] = useState(''); // Estado para el nombre
  const [email, setEmail] = useState(''); // Estado para el correo electrnico
  const [message, setMessage] = useState(''); // Estado para el mensaje
  const [error, setError] = useState(''); // Estado para manejar errores
  const [success, setSuccess] = useState(false); // Estado para manejar el xito del envo

  // Funcin para manejar el envo del formulario
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault(); // Previene el comportamiento por defecto del formulario
    setError(''); // Resetea el error al enviar

    // Validacin para asegurarse de que todos los campos estn completos
    if (!name || !email || !message) {
      setError('Por favor, completa todos los campos.'); // Muestra un mensaje de error si falta algn campo
      return;
    }

    console.log({ name, email, message }); // Muestra los datos del formulario en la consola
    setSuccess(true); // Cambia el estado de xito a verdadero
    onThankYou(); // Llama a la funcin de agradecimiento pasada como prop
    onClose(); // Cierra el formulario
  };

  return (
    <Card 
      sx={{ 
        maxWidth: 400, // Ancho mximo de la tarjeta
        background: "linear-gradient(to right, #00bfff, #20b2aa)", // Gradiente de fondo
        boxShadow: 3, // Sombra de la tarjeta
        borderRadius: '16px', // Bordes redondeados
        padding: 3, // Padding interno
        marginTop: 2, // Espaciado superior
        textAlign: 'center', // Texto centrado
      }}
    >
      <Typography variant="h6" gutterBottom>Contacto</Typography> {/* Ttulo del formulario */}
      {success && <Typography color="success.main">Mensaje enviado con xito!</Typography>} {/* Mensaje de xito */}
      {error && <Typography color="error.main">{error}</Typography>} {/* Mensaje de error */}
      <form onSubmit={handleSubmit}> {/* Maneja el evento de envo del formulario */}
        <TextField
          label="Nombre" // Etiqueta del campo
          variant="outlined" // Estilo del campo
          fullWidth // Ancho completo
          margin="normal" // Margen normal
          value={name} // Valor del campo de nombre
          onChange={(e) => setName(e.target.value)} // Actualiza el estado de nombre al cambiar
          sx={{ 
            backgroundColor: '#a0d7f0', // Color de fondo del campo
            '& .MuiOutlinedInput-root': {
              '& fieldset': {
                borderColor: '#a0d7f0', // Color del borde
              },
              '&:hover fieldset': {
                borderColor: '#80c2e6', // Color del borde al pasar el mouse
              },
              '&.Mui-focused fieldset': {
                borderColor: '#007bbf', // Color del borde cuando el campo est enfocado
              },
            },
          }} 
        />
        <TextField
          label="Correo Electrnico" // Etiqueta del campo
          variant="outlined" // Estilo del campo
          fullWidth // Ancho completo
          margin="normal" // Margen normal
          type="email" // Tipo de campo
          value={email} // Valor del campo de correo electrnico
          onChange={(e) => setEmail(e.target.value)} // Actualiza el estado de email al cambiar
          sx={{ 
            backgroundColor: '#a0d7f0', // Color de fondo del campo
            '& .MuiOutlinedInput-root': {
              '& fieldset': {
                borderColor: '#a0d7f0', // Color del borde
              },
              '&:hover fieldset': {
                borderColor: '#80c2e6', // Color del borde al pasar el mouse
              },
              '&.Mui-focused fieldset': {
                borderColor: '#007bbf', // Color del borde cuando el campo est enfocado
              },
            },
          }} 
        />
        <TextField
          label="Mensaje" // Etiqueta del campo
          variant="outlined" // Estilo del campo
          fullWidth // Ancho completo
          margin="normal" // Margen normal
          multiline // Permite mltiples lneas
          rows={4} // Nmero de filas visibles
          value={message} // Valor del campo de mensaje
          onChange={(e) => setMessage(e.target.value)} // Actualiza el estado de mensaje al cambiar
          sx={{ 
            backgroundColor: '#a0d7f0', // Color de fondo del campo
            '& .MuiOutlinedInput-root': {
              '& fieldset': {
                borderColor: '#a0d7f0', // Color del borde
              },
              '&:hover fieldset': {
                borderColor: '#80c2e6', // Color del borde al pasar el mouse
              },
              '&.Mui-focused fieldset': {
                borderColor: '#007bbf', // Color del borde cuando el campo est enfocado
              },
            },
          }} 
        />
        <Button 
          type="submit" // Tipo del botn
          variant="contained" // Estilo del botn
          color="secondary" // Color del botn, manteniendo el estilo del botn de contacto
          sx={{ marginTop: 2 }} // Margen superior
        >
          Enviar {/* Texto del botn */}
        </Button>
        <Button 
          variant="outlined" // Estilo del botn
          color="secondary" // Color del botn
          onClick={onClose} // Maneja el clic para cerrar el formulario
          sx={{ marginTop: 2, marginLeft: 2 }} // Mrgenes superior e izquierdo
        >
          Cerrar {/* Texto del botn */}
        </Button>
      </form>
    </Card>
  );
};

export default ContactForm; // Exporta el componente ContactForm para que pueda ser utilizado en otras partes de la aplicacin
